import { FixedArray } from 'memop';

export default class Scene {
  constructor() {
    this._lights = new FixedArray(16);
    this._objects = new FixedArray(16);
  }

  addObject(obj) {
    let idx = this._objects.indexOf(obj);
    if (idx === -1) {
      this._objects.push(obj);
    }
  }

  removeObject(obj) {
    let idx = this._objects.indexOf(obj);
    if (idx !== -1) {
      this._objects.fastRemove(idx);
    }
  }
}